.container-det {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  border: 0.5px solid white;
  border-radius: 20px;
  width: 971px;
}

.con-det-h {
  border-bottom: 0.5px solid white;
  width: 100%;
  padding: 20px 20px;
  display: flex;
  align-items: center;
  height: 100px;
}

.con-det-hd {
  font-family: 'Lato', sans-serif;
  font-weight: 500; /* Medium weight */
  font-style: normal; /* "Medium" is a weight, not a style */
  font-size: 28px;
  padding: 0 5px;
}

.con-det-hd-img {
  width: 30px;
  height: 36px;
}

.con-det-buts {
  margin-left: auto;
}

.cont {
  font-size: 50px;
  font-family: cursive;
  color: white;
  word-break: break-word;
}

.det-btn {
  width: 125px;
  height: 38px;
  border-radius: 12px;
  background-color: #2e2c2c;
  color: white;
  font-family: 'Lato', sans-serif;
  font-weight: 500; /* Medium weight */
  font-style: normal; /* Medium is a weight, not a style */
  font-size: 18px;
  cursor: pointer;
  margin-left: 10px;
}

.btn-logo {
  margin-right: 10px;
}

.cont-div {
  border-bottom: 0.5px solid white;
  width: 100%;
  height: 72px;
  display: flex;
  align-items: center;
}

.cont-key {
  width: 20%;
  display: flex;
  justify-content: center;
  font-size: 24px;
  font-family: 'Lato', sans-serif;
  font-weight: 500; /* Medium weight */
  font-style: normal; /* Medium is a weight, not a style */
}

.cont-val {
  width: 80%;
  display: flex;
  justify-content: center;
  font-size: 20px;
  font-family: 'Lato', sans-serif;
  font-weight: 400; /* Medium weight */
  font-style: normal; /* Medium is a weight, not a style */
}

.del-btn {
  background-color: red;
}

.checkbox-container {
  display: flex;
  align-items: center;
  margin: 10px;
}

input[type='checkbox'] {
  margin-right: 10px;
  width: 20px;
  height: 20px;
  accent-color: #007bff; /* Custom color for the checkbox (modern browsers) */
}

label {
  font-size: 16px;
  color: #333;
}

.cont-l {
  white-space: normal;
  word-break: break-word;
}
